
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Message, MessageService } from 'primeng/api';
import { environment } from 'src/environments/environment';
import { AuthService } from '../auth/auth.service';
import { AuthLoginInfo } from '../auth/models/login-info';
import { TokenStorageService } from '../auth/token-storage.service';

 
@Component({
  selector: 'app-profil',
  templateUrl: './profil.component.html',
  styleUrls: ['./profil.component.css'],
  providers: [MessageService],
})
export class ProfilComponent implements OnInit {
  active: boolean;  
  board: string;
  errorMessage: string;
  

  passwordForm: FormGroup; 
  profil:any = undefined;

  
  private loginInfo: AuthLoginInfo;

  constructor(private messageService:MessageService, private formBuilder: FormBuilder, private authService: AuthService, 
      private tokenStorage:TokenStorageService, private httpClient: HttpClient) { }

  ngOnInit() {
      /*this.userService.getUserBoard().subscribe(
        data => {
          this.board = data; 
        },
        error => {
          this.errorMessage = `${error.status}: ${JSON.parse(error.error).message}`;
        }
      );
        */
      this.httpClient.get(environment.url+'/authentification/api/profil',  {headers: new HttpHeaders({
       'Authorization': 'Bearer '+this.tokenStorage.getToken()
       })
     })
     .subscribe(res=>{ 
       this.profil = res;
       console.log(this.profil);
      }, err=>{
        console.log(" rr "+err.message);
       this.showError("message d'erreur ")
     });
     
     this.passwordForm = this.formBuilder.group({
         'ancienpassword' : new FormControl('',  Validators.compose([Validators.required, Validators.minLength(6)])),
         'password': new FormControl('',  Validators.compose([Validators.required, Validators.minLength(6)])),
         'confirmationpassword':new FormControl('',  Validators.compose([Validators.required, Validators.minLength(6)]))
     });
    }

  onClick(event) {
      this.active = !this.active;
      event.preventDefault();
  }

  logout()
  {
      this.tokenStorage.signOut();
      window.location.reload();
  }
  public message_error:any = undefined

  updatePassword(value:any) 
  {
  
    console.log(value);
    if(value['confirmationpassword'] != value['password'])
    {
     this.message_error = "la confirmation de mot de passe et le mot de passe doivent etre le meme ";
     this.showWarn(this.message_error);
    }else{
      this.httpClient.post(environment.url+'/authentification/api/update/password',value,  {headers: new HttpHeaders({
        'Authorization': 'Bearer '+this.tokenStorage.getToken()
        })
      })
      .subscribe(res=>{ 
        console.log(res);
        
        this.loginInfo = new AuthLoginInfo(
          this.profil['username'],
          value['password']);
     
        this.authService.attemptAuth(this.loginInfo).subscribe(
          data => {
            this.tokenStorage.saveToken(data.accessToken);
            this.tokenStorage.saveUsername(data.username);
            this.tokenStorage.saveAuthorities(data.authorities);    
            //            this.reloadPage();
          },
          error => {
            this.errorMessage = error.error.message;
            this.showError(this.errorMessage );
            //this.isLoginFailed = true;
          }
        );

        this.showSuccess("Vous avez mis  jour avec success votre mot de passe. Merci !");
        this.passwordForm.reset();
        
        //this.profil = res;
       }, err=>{
         if(err.status === 401)
         {
           this.showError(" Vous n'etes pas autoris  effectuer cette modification, veuillez vous deconnectez et vous reconnectez");
         }else{
            this.showError("message d'erreur "+err.message);
         }
      });
    }
    
  }
         
  /**
  *  costumisation des erreurs
  */

 msgs: Message[] = []; 

 showSuccess(message:string) {
 this.msgs = [];
 this.msgs.push({severity:'success', summary:'Message de success :', detail:''+message});
 }

 showInfo(message:string) {
 this.msgs = [];
 this.msgs.push({severity:'info', summary:'Message Info :', detail:''+message});
 }

 showWarn(message:string) {
 this.msgs = [];
 this.msgs.push({severity:'warn', summary:'Message d\'avertissement :', detail:''+message});
 }

 showError(message:string) {
 this.msgs = [];
 this.msgs.push({severity:'error', summary:'Message d\'erreur ', detail:''+message});
 }

}
