import { formatDate } from '@angular/common';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { MessageService } from 'primeng/api';
import { TokenStorageService } from 'src/app/auth/token-storage.service';
import { environment } from 'src/environments/environment.prod';
import { Workbook } from 'exceljs';
import * as fs from 'file-saver';
import * as Excel from "exceljs";

export interface rapportVenteForm{
  datedebut?,
  datefin?,
}


@Component({
  selector: 'app-rapportsvente',
  templateUrl: './rapportsvente.component.html',
  styleUrls: ['./rapportsvente.component.css'],
  providers:[MessageService]
})
export class RapportsventeComponent implements OnInit {


  rapportForm:FormGroup; 

  rapportVentes:any = undefined;
  rapportFormObject :rapportVenteForm = {};

  /**
   * 
   * @param messageService 
   * @param httpClient 
   * @param router 
   * @param tokenStorage 
   */
   constructor( private formBuilder: FormBuilder,  private messageService: MessageService, private httpClient:HttpClient, private router:Router, 
    private tokenStorage:TokenStorageService)  { }


  ngOnInit(): void {

    this.rapportForm  = this.formBuilder.group({
      //'nom': new FormControl('', Validators.required),
      'datedebut': new FormControl('', Validators.required),
      'datefin': new FormControl('', Validators.required),

    });

    const format = 'yyyy-MM-dd';    
    const format_date = 'dd';
    const locale = 'en-US';

    console.log(new Date().getUTCFullYear());

    this.rapportFormObject.datedebut =  formatDate('01/01/'+new Date().getUTCFullYear(),format,locale);
    
    this.rapportFormObject.datefin =  formatDate('12/31/'+new Date().getUTCFullYear(),format,locale);
    
    this.httpClient.post(environment.url+"/api/postal/vente/rapports", this.rapportFormObject,{headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe( response=>{

      console.log(response);

      this.rapportVentes = response;

    }, error=>{

    });
    

  }

  /**
   * 
   * @param value 
   */
  saveRapport(value:any)
  {
    console.log('je suis');
    console.log(value);

    const format = 'yyyy-MM-dd';    
    const format_date = 'dd';
    const locale = 'en-US';

    console.log(new Date().getUTCFullYear());

    this.rapportFormObject.datedebut =  formatDate(value['datedebut'],format,locale);
    
    this.rapportFormObject.datefin =  formatDate(value['datefin'],format,locale);
    
    this.httpClient.post(environment.url+"/api/postal/vente/rapports", this.rapportFormObject,{headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe( response=>{

      console.log(response);

      this.rapportVentes = response;

    }, error=>{

    });
  }


  exportExcel() {
    let Aplhabet = ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J', 'K', 'L', 'M', 'N', 'O', 'P', 'Q', 'R', 'S']
    import("xlsx").then(xlsx => {let workbook = new Excel.Workbook();
        this.rapportVentes.forEach(element => {
          console.log(element);
          const title = element.month;
          let entete = []
          //const header = element.entete
          //const data = element.
          //console.log(title); console.log(header);
          element.entete.forEach(element2 => {
            console.log(element2)
            entete.push({ header: element2, key: element2, width: 10, style: { font: { name: 'Trebuchet MS', size:12} } },)           
          });
          
            let worksheet = workbook.addWorksheet(title);

            let titre =  'VENTE '+title+'2021.';
           
            worksheet.getCell('A1').alignment = { horizontal:'center'} ;
            /** Ajout du titre au fichier excel */
            worksheet.columns = [ ...entete];

            /**
             * Ajout des donnes au fichier excel
             */

             element.ventes.forEach(e => {
              worksheet.addRow([e.date, ...e.ventes], 'n');
            });
       
            const row = worksheet.lastRow;
            worksheet.getRows(1, 32).forEach(ee=>{
              ee.eachCell(function(cell, colNumber) {
                cell.border = {
                  top: {style:'thin'},
                  left: {style:'thin'},
                  bottom: {style:'thin'},
                  right: {style:'thin'}
                };
              });
            })
    });

    

    workbook.xlsx.writeBuffer().then((data) => {
      let blob = new Blob([data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8' });      
      fs.saveAs(blob, 'ProductData.xlsx');
    });
  });
}

saveAsExcelFile(buffer: any, fileName: string): void {
    import("file-saver").then(FileSaver => {
        let EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
        let EXCEL_EXTENSION = '.xlsx';
        const data: Blob = new Blob([buffer], {
            type: EXCEL_TYPE
        });
        FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
    });
}

getDatas() {
  let datas = [];

  console.log(this.rapportVentes);
  return datas;
}
  



}
