import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Message, MessageService } from 'primeng/api';
import { TokenStorageService } from 'src/app/auth/token-storage.service';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-livraisonechoue',
  templateUrl: './livraisonechoue.component.html',
  styleUrls: ['./livraisonechoue.component.css'],
  providers:[MessageService]
})
export class LivraisonechoueComponent implements OnInit {

  private msgs:Message[] = [];


  private liste:any = undefined;

  constructor(private messageService: MessageService, private httpClient:HttpClient, private router:Router, 
    private tokenStorage:TokenStorageService) { }

  ngOnInit(): void {

    /**
     *  -- REQUETE POUR RECUPERER LA LISTE DES TYPE D ARTICLE
     */
    this.httpClient.get(environment.url+"/api/postal/livraison/echec", {headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe(response=>{
      this.liste = response;
      console.log(this.liste);
    }, error=>{      
      this.showWarn("La liste n'a pas pu etre affich !!! voici la raison - "+error.message);
    });

  }

  search(value:any)
  {
    this.router.navigate(['gestion/stocks/recherche?4aa7d2d064588a6e7db6d69ffcc400f402863af69afdf0b2925cc2e45953c869'],{
      queryParams:{id:''+value["reference"]+''}
    });

  }

  exportColumns:any = [];
  exportPdf() {
    import("jspdf").then(jsPDF => {
        import("jspdf-autotable").then(x => {
            const doc =  new jsPDF.default({
              orientation: "landscape",
            });
            (doc as any).autoTable(this.exportColumns, this.getDatas());
            doc.save('primengTable.pdf');
        })
    })
  }

  exportExcel() {
      import("xlsx").then(xlsx => {
          const worksheet = xlsx.utils.json_to_sheet(this.getDatas());
          const workbook = { Sheets: { 'data': worksheet }, SheetNames: ['data'] };
          const excelBuffer: any = xlsx.write(workbook, { bookType: 'xlsx', type: 'array' });
          this.saveAsExcelFile(excelBuffer, "livraison_echoue");
      });
  }

  saveAsExcelFile(buffer: any, fileName: string): void {
      import("file-saver").then(FileSaver => {
          let EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
          let EXCEL_EXTENSION = '.xlsx';
          const data: Blob = new Blob([buffer], {
              type: EXCEL_TYPE
          });
          FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
      });
  }

  getDatas() {
    let datas = [];
  
    return this.liste;
  }

    /**
   *  costumisation des erreurs
   */

     showSuccess(message:string) {
      this.msgs = [];
      this.msgs.push({severity:'success', summary:'Message de success :', detail:''+message});
    }
  
    showInfo(message:string) {
      this.msgs = [];
      this.msgs.push({severity:'info', summary:'Message Info :', detail:''+message});
    }
  
    showWarn(message:string) {
      this.msgs = [];
      this.msgs.push({severity:'warn', summary:'Message d\'avertissement :', detail:''+message});
    }
  
    showError(message:string) {
      this.msgs = [];
      this.msgs.push({severity:'error', summary:'Message d\'erreur ', detail:''+message});
    }


}