import { formatDate } from '@angular/common';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Message, MessageService } from 'primeng/api';
import { Dropdown } from 'src/app/apps/model/dropdown';
import { TokenStorageService } from 'src/app/auth/token-storage.service';
import { Select } from 'src/app/model/Select';
import { environment } from 'src/environments/environment.prod';
import { EnvoisListDTO } from '../envoisdto';





@Component({
  selector: 'app-tableaubord',
  templateUrl: './tableaubord.component.html',
  styleUrls: ['./tableaubord.component.css'],
  providers:[MessageService]
})
export class TableaubordComponent implements OnInit {

  /***
   * 
   */

   msgs:Message[] = []; 
  
   tableaubord1:any = undefined;
   basicData:any =undefined;
   listems:any = undefined;



  /**
   * 
   * @param formBuilder 
   * @param messageService 
   * @param httpClient 
   * @param router 
   * @param tokenStorage 
   */
  constructor(private formBuilder: FormBuilder, private messageService: MessageService, private httpClient:HttpClient, private router:Router, 
    private tokenStorage:TokenStorageService)  { }


    
  ngOnInit(): void {

    this.httpClient.get(environment.url+"/api/postal/envoi/tableau/bord1",{headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe( (response:any)=>{
      console.log(response);
      let labels:any [] =[];
      let results : any [] =[];
      let colors:any [] = [];
      let i =1;
      response.forEach(element => {
          labels.push(element['type']);
          results.push(element['COUNT']);
          colors.push(element['color']);
          i++;
      });
      this.tableaubord1 = {
        labels: [...labels],
        datasets: [
            {
                data: [...results],
                backgroundColor: [
                  "#64B5F6",
                  "#81C784",
                  "#FFB74D"
                ],
                hoverBackgroundColor: [
                    "#64B5F6",
                    "#81C784",
                    "#FFB74D"
                ]
            }
        ]
    };
    }, error=>{
        this.showWarn(" une erreur c'est produit et le systme selectionner le type de ventes - La raison est voici : "+error.message);
    });


    this.httpClient.get(environment.url+"/api/postal/envoi/tableau/bord2",{headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe((response:any)=>{
      console.log(response);
         let datas :any = [];

         const result = Object.keys(response).forEach(key => {
          var obj1 = response[key];
            console.log('key is ' + key);
            
            let _postsArray: Array<any> = [];
            let  _postsColor: Array<any> = [];

            const sousresult = Object.keys(obj1).forEach(key1 => {
                _postsArray.push(obj1[key1]['value']);
                _postsColor.push(obj1[key1]['color'])
            });
            console.log(_postsArray);

            Promise.all(['sousresult']).then( ()=>{
              datas.push( {
                label: ''+key,
                data: [..._postsArray],
                fill: false,
                backgroundColor: _postsColor[0],
                borderColor:_postsColor[0],
                tension: .4
              } )
            });
         })
         
         Promise.all(['result']).then( ()=>{ 
           console.log(datas);
           this.basicData = {
            labels: ['Janvier', 'Fevrier', 'Mars', 'Avril', 'Mai', 'Juin', 'Juillet', 'Aout', 'Septembre', 'Octobre', 'Novembre','Decembre'],
            datasets: [
              ...datas
            ]
          };
         });
    }, error=>{
      this.showWarn(" une erreur c'est produit et le systme selectionner le type de ventes - La raison est voici : "+error.message);
  });
    /**
     *  -- REQUETE POUR RECUPERER LA LISTE DES TYPE D ARTICLE
     */
    this.httpClient.get(environment.url+"/api/postal/envoi/ems", {headers: new HttpHeaders({
      'Authorization': 'Bearer '+this.tokenStorage.getToken()
      })
    }).subscribe(response=>{
      this.listems = response;
      console.log(this.listems);
    }, error=>{      
      this.showWarn("Les articles EMS  n'ont pas pu etre charg, Voici la raison "+error.getMessage());
    });

  }
  displayMaximizable: boolean;
  ems:any = undefined;

  show(ems:any)
  {
    this.displayMaximizable = true;
    this.ems = ems;
  }

  exportExcel() {
    import("xlsx").then(xlsx => {
        const worksheet = xlsx.utils.json_to_sheet(this.getDatas());
        const workbook = { Sheets: { 'data': worksheet }, SheetNames: ['data'] };
        const excelBuffer: any = xlsx.write(workbook, { bookType: 'xlsx', type: 'array' });
        this.saveAsExcelFile(excelBuffer, "primengTable");
    });
}

saveAsExcelFile(buffer: any, fileName: string): void {
    import("file-saver").then(FileSaver => {
        let EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
        let EXCEL_EXTENSION = '.xlsx';
        const data: Blob = new Blob([buffer], {
            type: EXCEL_TYPE
        });
        FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
    });
}

getDatas() {
  let datas = [];
  for(let envoi of this.listems) {
    let envoiedto : EnvoisListDTO = {};
   envoiedto.reference = envoi['reference'];
    envoiedto.type = envoi['type']; 
    
    envoiedto.name = envoi['name']; 
    envoiedto.adresse = envoi['adresse']; 
    envoiedto.email = envoi['email']; 
    envoiedto.nomsender = envoi['nomsender']; 
    envoiedto.telexpediteur = envoi['telexpediteur']; 

    
    envoiedto.pays = envoi['pays']; 
    envoiedto.namerecipient = envoi['namerecipient']; 
    envoiedto.telrecipient = envoi['telrecipient']; 

    envoiedto.createdat = envoi['createdat']['name']; 
    envoiedto.createdat = envoi['createdat']; 
    envoiedto.created = envoi['created']['name']; 
    envoiedto.updatedat = envoi['updatedat']; 
    envoiedto.updated = envoi['updated']['name']; 
    console.log(envoi);
    datas.push(envoiedto);
  }
  return datas;
}
  /**
   *  costumisation des erreurs
   */

   showSuccess(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'success', summary:'Message de success :', detail:''+message});
  }

  showInfo(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'info', summary:'Message Info :', detail:''+message});
  }

  showWarn(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'warn', summary:'Message d\'avertissement :', detail:''+message});
  }

  showError(message:string) {
    this.msgs = [];
    this.msgs.push({severity:'error', summary:'Message d\'erreur ', detail:''+message});
  }



}
